import React, { useEffect } from 'react';
import { Link as RouterLink, useHistory, useLocation } from 'react-router-dom';

// material-ui
import { useTheme } from '@material-ui/core';
import { Grid, Stack, Typography, useMediaQuery, Box, Button } from '@material-ui/core';

// project imports
import AuthWrapper1 from './../AuthWrapper1';
// import Logo from './../../../ui-component/Logo';
import AuthCardWrapper from './../AuthCardWrapper';
import OtpInput from 'react-otp-input';
import AnimateButton from '../../../ui-component/extended/AnimateButton';
import { useDispatch } from 'react-redux';
import { setMessage } from '../../../store/actions/MessageActions';
import { verifyOtp } from '../../../store/actions/AuthActions/Register.actions';
import { useAuth } from '../../../hooks/useAuth';

// styles
const styles = {
    width: '100%',
    borderRadius: 8,
    border: '2px solid #3fb2cf'
};
const containerStyles = {
    width: '70%',
    margin: 'auto'
};

//================================|| OTP Verify ||================================//

const VerifyOtp = () => {
    const theme = useTheme();
    const matchDownSM = useMediaQuery(theme.breakpoints.down('sm'));
    const [otp, setOtp] = React.useState('');
    const auth = useAuth();
    const handleChange = (otp) => setOtp(otp);
    const dispatch = useDispatch();
    const location = useLocation();
    const history = useHistory();

    useEffect(() => {
        if (!location?.state?.phoneNumber) {
            history.push('/login-otp');
        }
    }, [location?.state?.phoneNumber, history]);

    const onSubmit = (event) => {
        event.preventDefault();
        if (otp.length === 6) {
            auth.signin(
                {
                    username: location?.state?.phoneNumber,
                    password: otp
                },
                () => {},
                () => {}
            );
        } else {
            dispatch(setMessage('OTP is required'));
        }
    };

    return (
        <AuthWrapper1>
            <Grid container direction="column" justifyContent="flex-end" sx={{ minHeight: 'calc(100vh - 68px)' }}>
                <Grid item xs={12}>
                    <Grid container justifyContent="center" alignItems="center" sx={{ minHeight: 'calc(100vh - 68px)' }}>
                        <Grid item sx={{ m: { xs: 1, sm: 3 }, mb: 0 }}>
                            <AuthCardWrapper>
                                <Grid container spacing={2} alignItems="center" justifyContent="center">
                                    <Grid item sx={{ mb: 3 }}>
                                        <RouterLink to="#" style={{ textDecoration: 'none' }}>
                                            {/* <Logo /> */}
                                            <Typography color={theme.palette.primary.main} gutterBottom variant={matchDownSM ? 'h3' : 'h2'}>
                                                BOOKEINSTEIN
                                            </Typography>
                                        </RouterLink>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Grid
                                            container
                                            direction={matchDownSM ? 'column-reverse' : 'row'}
                                            alignItems="center"
                                            justifyContent="center"
                                        >
                                            <Grid item>
                                                <Stack alignItems="center" justifyContent="center" spacing={1}>
                                                    <Typography
                                                        color={theme.palette.primary.main}
                                                        gutterBottom
                                                        variant={matchDownSM ? 'h4' : 'h3'}
                                                    >
                                                        Enter Verification code
                                                    </Typography>
                                                </Stack>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                    <form onSubmit={onSubmit}>
                                        <Grid item xs={12}>
                                            <OtpInput
                                                value={otp}
                                                onChange={handleChange}
                                                numInputs={6}
                                                isInputNum={true}
                                                separator={<span>-</span>}
                                                containerStyle={containerStyles}
                                                inputStyle={styles}
                                                shouldAutoFocus={true}
                                            />
                                        </Grid>
                                        <Grid item xs={12}>
                                            <Box
                                                sx={{
                                                    mt: 2
                                                }}
                                            >
                                                <AnimateButton>
                                                    <Button
                                                        type="submit"
                                                        disableElevation
                                                        fullWidth
                                                        size="large"
                                                        variant="contained"
                                                        color="primary"
                                                    >
                                                        SUBMIT
                                                    </Button>
                                                </AnimateButton>
                                            </Box>
                                        </Grid>
                                    </form>
                                </Grid>
                            </AuthCardWrapper>
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
        </AuthWrapper1>
    );
};

export default VerifyOtp;
